function deg2rad(degree) {
  return degree / 180.0 * Math.PI;
}

function genPath(angle, xStart, roundSizeLeft, roundSize, width, height) {
  var C = roundSizeLeft * Math.tan(deg2rad(angle / 2.0));
  var A = C * Math.cos(deg2rad(angle));
  var B = C * Math.sin(deg2rad(angle));
  var D = roundSizeLeft * Math.tan(deg2rad((90 - angle) / 2.0));
  var E = D * Math.cos(deg2rad(angle));
  var F = C * Math.sin(deg2rad(angle));
  var startPointX = xStart - A;
  var startPointY = B;
  var yStart = xStart * Math.tan(deg2rad(angle));
  var pathStr = "M".concat(startPointX, ",").concat(startPointY);
  pathStr += "a".concat(roundSizeLeft, ",").concat(roundSizeLeft, ",0,0,1,").concat(A + C, ",").concat(-B);
  pathStr += "h".concat(width - xStart - C - roundSize);
  pathStr += "a".concat(roundSize, ",").concat(roundSize, ",0,0,1,").concat(roundSize, ",").concat(roundSize);
  pathStr += "v".concat(height - 2 * roundSize);
  pathStr += "a".concat(roundSize, ",").concat(roundSize, ",0,0,1,").concat(-roundSize, ",").concat(roundSize);
  pathStr += "H".concat(roundSize);
  pathStr += "a".concat(roundSize, ",").concat(roundSize, ",0,0,1,").concat(-roundSize, ",").concat(-roundSize);
  pathStr += "V".concat(yStart + D);
  pathStr += "a".concat(roundSizeLeft, ",").concat(roundSizeLeft, ",0,0,1,").concat(E, ",").concat(-D - F);
  pathStr += "Z";
  return pathStr;
}

function vh2px(v) {
  var h = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);
  return v * h / 100;
}

function vw2px(v) {
  var w = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
  return v * w / 100;
}

function vmin2px(v) {
  return Math.min(vh2px(v), vw2px(v));
}

function vmax2px(v) {
  return Math.max(vh2px(v), vw2px(v));
}

function processString(unitString) {
  if (unitString.includes("px")) {
    var s = unitString.slice(0, -2);
    return parseFloat(s);
  }

  if (unitString.includes("vh")) {
    var _s = unitString.slice(0, -2);

    return vh2px(parseFloat(_s));
  }

  if (unitString.includes("vw")) {
    var _s2 = unitString.slice(0, -2);

    return vw2px(parseFloat(_s2));
  }

  if (unitString.includes("vmin")) {
    var _s3 = unitString.slice(0, -2);

    return vmin2px(parseFloat(_s3));
  }

  if (unitString.includes("vmax")) {
    var _s4 = unitString.slice(0, -2);

    return vmax2px(parseFloat(_s4));
  }

  if (unitString.includes("%")) {
    var _s5 = unitString.slice(0, -1);

    return vw2px(parseFloat(_s5));
  }

  return 0; //error
}

export default {
